"use client";

import React, { useEffect, useState } from "react";
import ExamProgressBar from "./ExamProgressBar";
import ExamQuestion from "./ExamQuestion";
import ExamResults from "./ExamResults";
import questionsData from "../../progress/module/data/questions";

interface ExamModalProps {
  isOpen: boolean;
  onClose: () => void;
  moduleId: number;
}

const ExamModal: React.FC<ExamModalProps> = ({ isOpen, onClose, moduleId }) => {
  useEffect(() => {
    if (isOpen) document.body.style.overflow = "hidden";
    else document.body.style.overflow = "auto";
  }, [isOpen]);

  if (!isOpen) return null;

  const allQuestions = questionsData[moduleId] || [];
  const totalQuestions = allQuestions.length;
  const passingScore = 9;
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [answers, setAnswers] = useState<string[]>([]);
  const [showResults, setShowResults] = useState(false);

  const handleAnswer = (answer: string) => {
    setAnswers([...answers, answer]);

    if (currentQuestionIndex + 1 < allQuestions.length) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    } else {
      setShowResults(true);
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-80 flex justify-center items-center z-50">
      <div className="bg-white w-full h-full overflow-y-auto p-8 relative">
        <button onClick={onClose} className="absolute top-4 right-4 bg-red-500 text-white p-2 rounded-lg">‚úñ Cerrar</button>

        {showResults ? (
          <ExamResults totalQuestions={totalQuestions} correctAnswers={answers.length} />
        ) : (
          <>
            <ExamProgressBar current={currentQuestionIndex + 1} total={totalQuestions} />
            <ExamQuestion question={allQuestions[currentQuestionIndex]} onAnswer={handleAnswer} questionNumber={currentQuestionIndex + 1} />
          </>
        )}
      </div>
    </div>
  );
};

export default ExamModal;
