"use client";
import React from "react";

const ExamQuestion = ({ question, options, onSelectAnswer }) => {
  // üî¥ Verificar que onSelectAnswer es una funci√≥n antes de ejecutarla
  if (!onSelectAnswer || typeof onSelectAnswer !== "function") {
    console.error("‚ùå Error: onSelectAnswer no es una funci√≥n en ExamQuestion.");
    return <p className="text-red-500">Error: no se puede seleccionar respuesta.</p>;
  }

  return (
    <div className="mb-4 p-4 border rounded-lg shadow-md">
      <h3 className="font-semibold text-lg mb-2">{question}</h3>
      <div className="space-y-2">
        {options.map((option, index) => (
          <button
            key={index}
            className="w-full p-2 border rounded bg-gray-200 hover:bg-gray-300 transition"
            onClick={() => onSelectAnswer(option)} // ‚úÖ Se ejecuta correctamente
          >
            {option}
          </button>
        ))}
      </div>
    </div>
  );
};

export default ExamQuestion;
